<template>
  <div style="max-width: 100%;box-sizing: border-box;" class="feedlist">
     <myHeader :isBack="true" title="销售通知">

    </myHeader>
    <div class="mt4 list-block">
      <p>
        "{{name}}养户您好，{{noticeList.bizYMD}}{{noticeList.bizHIS}}，对接员{{noticeList.name}}将带客户{{noticeList.customer}}回收猪只{{noticeList.qty}}头，请您提前做好猪只控料工作及相应准备工作"
      </p>
      <div class="code">
        <!--<img src="../../../assets/photo-placeholder.png" alt>-->
        <div id="qrcode"></div>
        <p>根据销售通知随机自动生成</p>
      </div>
      <div class="notice">确定开栏销售，请将二维码展示给对接员</div>
    </div>
  </div>
</template>
<script>
import myHeader from "@/components/Header";
import QRCode from 'qrcodejs2'
import {getSalesRecord} from '@/api/business'
import  {getParam} from '@/utils/wxUtil'
let Base64 = require('js-base64').Base64;
export default {
  name: "salesNotice",
  components: {
    myHeader
  },
  data(){
    return {
      noticeList:{
      },
      name:'某某',
    }
  },
  created(){
    this.getDetail();
    this.name = JSON.parse(sessionStorage.getItem('userInfo')).name;
  },
  methods:{
    qrcode(url) {
      let qrcode = new QRCode('qrcode', {
        width: 190,
        height: 190,
        text: url, //
        colorDark : "#000",
        colorLight : "#fff",
      })
    },
    getDetail(){
      // let id = this.$route.params.recordId;
      const rcordId= getParam('rcordId').replace(/%3D/g,'=').trim();
      getSalesRecord(rcordId).then((res)=>{
        this.noticeList = res.data;
        this.qrcode(res.data.rcordId);
      }).catch((err)=>{
        console.log(err)
      })
    }
  }
};
</script>
<style lang="scss" scoped>
.feedlist {
  min-height: 100vh;
  background-color: #f8f8f8;
  padding: 0.12rem 0;
  .list-block {
    font-size: 0.16rem;
    background-color: #fff;
    padding: 0.1rem;
    .code {
      width: 2rem;
      margin: 0.16rem auto;
    }
    .notice {
      width: 3rem;
      margin: 0 auto;
    }
  }

  #qrcode {
    display: inline-block;
    img {
      width: 1.9rem;
      height: 1.9rem;
      background-color: #fff; //设置白色背景色
      padding: .06rem; // 利用padding的特性，挤出白边
    }
  }
}
</style>



